@charset "UTF-8";
*,
*::after,
*::before {
  box-sizing: border-box;
}

:root {
  --background-color-body: #011e3c;
  --background-color-button: #ffffff;
  --btn-close-hover: #663399;
  --background-color-footer: #081a2e;
  --footer-text-color: #ebedf0;
  --text-manual: #444444;
  --outline-plum: #dda0dd;
  --_pink: #ffc2cc;
  --_white: #ffffff;
  --_dim-gray: #6969694d;
}

[data-theme=light] {
  --background-color-body: #fff5ee;
  --background-color-button: #e6e6e6;
  --background-color-footer: #303846;
}

html {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  min-width: 320px;
  min-height: 100%;
  font-family: "ABeeZee", sans-serif;
  font-size: 16px;
  line-height: 1em;
  background-color: var(--background-color-body);
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.main {
  position: relative;
  padding-top: 75px;
  flex-grow: 1;
}

.button-theme {
  position: absolute;
  top: 22px;
  right: 40px;
  display: inline-block;
  padding: 7px 10px;
  min-width: 120px;
  background-color: var(--background-color-button);
  border: none;
  border-radius: 5px;
  z-index: 99;
  cursor: pointer;
}
.button-theme:hover {
  opacity: 0.8;
}

h1 {
  margin: 0;
  margin-top: 40px;
  margin-bottom: 40px;
  font-size: 76px;
  line-height: 1em;
  text-align: center;
  color: var(--_pink);
}

@font-face {
  font-family: " ABeeZee";
  src: url("../fonts/ABeeZee-Regular.woff2") format("woff2"), url("../fonts/ABeeZee-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
.main {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.container {
  width: 100%;
  padding-left: 10px;
  padding-right: 10px;
  margin: 0 auto;
}
@media (min-width: 600px) {
  .container {
    max-width: 570px;
  }
}

.todo {
  position: relative;
  display: flex;
  flex-direction: column;
}

.todo__new {
  margin: 0;
  padding: 16px 16px 16px 60px;
  display: flex;
  background-color: var(--_white);
  border: none;
  box-shadow: inset 0 -2px 1px rgba(192, 192, 192, 0.6);
  font-size: 2rem;
  width: 100%;
  position: relative;
}
.todo__new:focus {
  outline: 0;
}

.todo__label {
  position: absolute;
  top: 75%;
  left: 5px;
  display: block;
  width: 40px;
  height: 40px;
  cursor: pointer;
}

.todo__button {
  width: 1px;
  height: 1px;
  border: none;
  opacity: 0;
  position: absolute;
  right: 100%;
  bottom: 100%;
}

.todo__button + label {
  width: 60px;
  height: 34px;
  font-size: 0;
  position: absolute;
  top: -52px;
  left: -10px;
  transform: rotate(90deg);
}

.todo__button + .todo__button--control::before {
  content: "❯";
  padding: 10px 27px;
  font-size: 22px;
  color: #e6e6e6;
  cursor: pointer;
}

.todo__button:hover + .todo__button--control::before {
  color: #737373;
}

.todo__button:checked + .todo__button--control::before {
  color: #737373;
}

.todo-main {
  position: relative;
  z-index: 2;
}

.todo__list {
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;
}

.todo__item {
  position: relative;
  padding-left: 60px;
  font-size: 20px;
  background-color: var(--_white);
}
.todo__item:not(:last-child) {
  border-bottom: 1px solid hsla(0, 0%, 41.2%, 0.3);
}
.todo__item--completed {
  text-decoration: line-through;
  color: #e6e6e6;
}

.todo__text {
  display: block;
  margin: 0;
  padding-top: 15px;
  padding-bottom: 15px;
  padding-right: 60px;
  word-break: break-all;
}
.todo__text:focus {
  outline: none;
  border: 1px solid #98fb98;
}

.todo__item-input + span {
  margin: 0;
  position: absolute;
  left: 5px;
  top: 50%;
  transform: translateY(-50%);
  width: 30px;
  height: 30px;
  border: 1px solid var(--_dim-gray);
  border-radius: 50%;
  z-index: 11;
  cursor: pointer;
}

.todo__item-input:hover + span {
  outline: 1px solid var(--outline-plum);
}

.todo__item-input:checked + span::before {
  content: "";
  width: 16px;
  height: 16px;
  position: absolute;
  background-color: hsl(187, 51.9%, 79.6%);
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  display: block;
  border-radius: 50%;
  z-index: 10;
}

.todo__item-close {
  padding: 0;
  position: absolute;
  right: 10px;
  bottom: 0;
  top: 50%;
  transform: translateY(-50%);
  background-color: transparent;
  border: 0;
  width: 40px;
  height: 40px;
  font-size: 30px;
  cursor: pointer;
  color: var(--outline-plum);
  display: none;
  transition: color 0.3s ease-out;
}
.todo__item-close::after {
  content: "×";
}
@media (any-hover: hover) {
  .todo__item-close:hover {
    color: var(--btn-close-hover);
  }
}

.todo__item:hover .todo__item-close {
  display: block;
}

.todo__item:hover .todo__item-close .edit {
  display: none;
}

.todo-main__footer {
  min-height: 20px;
  background-color: var(--_white);
  border-top: 1px solid hsl(0, 0%, 90%);
  padding: 10px 15px;
}

.todo-main__count {
  font-size: 1rem;
  line-height: 1em;
}
@media (max-width: 600px) {
  .todo-main__count {
    font-size: 0.6rem;
  }
}

.todo-main__footer-inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 520px;
  width: 100%;
}

.todo-main__manual {
  margin: 0;
  margin-top: 20px;
  font-size: 1.2rem;
  font-weight: 400;
  line-height: 1em;
  color: var(--text-manual);
}

.filters {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}
@media (min-width: 600px) {
  .filters {
    gap: 10px;
  }
}

.filters__link {
  display: inline-block;
  font-size: 0.6rem;
  line-height: 1em;
  text-decoration: none;
  color: #000000;
  padding: 3px 7px;
  border: 1px solid transparent;
  border-radius: 3px;
}
.filters__link--active {
  border-color: var(--outline-plum);
  pointer-events: none;
}
.filters__link:hover {
  border-color: var(--outline-plum);
}

@media (min-width: 767px) {
  .filters__link {
    font-size: 1rem;
  }
}
.filters__button {
  display: inline-block;
  padding: 0;
  border: none;
  background-color: transparent;
  font-size: 0.7rem;
  cursor: pointer;
}
.filters__button:hover {
  text-decoration: underline;
}

@media (min-width: 600px) {
  .filters__button {
    font-size: 0.9rem;
  }
}
.footer {
  margin-top: 40px;
  background-color: var(--background-color-footer);
  color: var(--footer-text-color);
  padding: 20px;
}
.footer > div {
  display: flex;
  align-items: center;
  justify-content: center;
}